import React from 'react'

export default function Slider(props) {
    const { children, overideIndex } = props;
    const [index, setIndex] = React.useState(0);

    React.useEffect(() => {
        let i = index;
        let myInterval = setInterval(() => {
            i++;
            if (i >= children.length) {
                i = 0;
            }
            setIndex(i);
        }, 3000)
        return () => {
            clearInterval(myInterval);
        };
    }, [overideIndex]);
    //i === index ? 'scale(1);' : 'scale(0);'
    console.log(props)
    const elements = children.map((e, i) => (
        <div style={{
            position:'absolute',
            transitionProperty:'width, opacity, transform, display, height',
            transitionDuration:'2s',
            height:i === index ? 460 : 0,
            overflow:'hidden',
            // display:i === index ? 'flex': 'none',
            flexGrow:1,
            flexDirection:'column',
            transform:  i === index ? 'scale(1)':(i > index || index === 0) ? 'scale(1)' : 'scale(1)',
            opacity: i === index ? 1: 0,
            overflow:'hidden',
        }}>
            {e}
        </div>
    ));
    return (
        <div>
            {elements}
        </div>
    )
}
