import React from 'react'

export const Card = (props) => {
    const [height, setHeight] = React.useState(false);
    return (
        <div class="col-12 col-sm-6 col-md-4"
            style={{
                cursor: 'pointer',
                height: height ? 650 : 350,
                transitionProperty: 'width, opacity, transform, display, height',
                transitionDuration: '0.7s',
                opacity: height ? 1 : 0.7,
            }} onMouseEnter={() => setHeight(true)} onClick={() => setHeight(!height)} onMouseLeave={() => setHeight(false)}>
            <div class="text-left">
                <div
                style={{
                    transitionProperty: ' height, filter, border-radius',
                    transitionDuration: '1s',
                    height: !height ? 160 : 200,
                    overflow:'hidden',
                    borderRadius: !height ? 15 : 10,
                    display:'flex',
                    justifyItems:'center',
                }}
                >
                    <img
                        style={{
                            transitionProperty: ' opacity, filter',
                            transitionDuration: '0.7s',
                            filter: !height ? 'contrast(90%)' : 'contrast(200%)',
                            opacity: height ? 1 : 0.7,
                            backgroundSize: 'contain',
                            height:300,
                        }}
                        class="card-img-top" src={props.img} alt="" />
                </div>
                <div class="card-body"

                >
                    <h3 class=" white-text card-title">{props.name}</h3>
                    <p class="white-text card-text p-2"
                        style={{
                            borderRadius: 10,
                            maxHeight: height ? 250 : 100,
                            overflow: 'hidden',
                            transitionProperty: 'width, opacity, transform, display, max-height',
                            transitionDuration: '0.7s',
                            opacity: height ? 0.6 : 0.1,
                            background: '#444',
                            alignContent: 'center',
                            display: 'flex',
                        }}
                    >{props.details}</p>
                </div>
            </div>
        </div>
    )
}
